import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import {AuthService} from '../../services/auth/auth.service';
@Component({
  selector: 'app-pointage',
  templateUrl: './pointage.component.html',
  styleUrls: ['./pointage.component.scss']
})
export class PointageComponent implements OnInit {
  interval;
  user: Object;
  punchIn: boolean = true;
  punchOut: boolean = false;
  breakIn: boolean = true;
  breakOut: boolean = false;
  Iduser : any;
  date: Date;
  userActivities = [{'activityType' : 'push in' , 'time' : '10:30'}]
  constructor(private snackBar: MatSnackBar ,   private authservice: AuthService) {   this.getiduserconected() }

  ngOnInit(): void {
  
  console.log(this.Iduser)

      this.interval = setInterval(this.clock, 1000); 
    this.clock();
    this.getActivities()
  }
  ngOnDestroy(){
    if(this.interval){
      clearInterval(this.interval);
    }
  }
getiduserconected(){
  this.Iduser = this.authservice.getIdfromToken()
}
  storeActivity(actType, time){
 
    this.authservice.addUserActivity(this.Iduser, actType, this.date, time)
    .subscribe( data => {
      if(data.success){
        this.snackBar.open('Mise à jours avec succès', 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack'],
        })
      }else{
        this.snackBar.open('Une erreur c\'est produite', 'X', {
          duration: 6000,
          verticalPosition: 'top',
          panelClass:['custom-snack-error'],
        });
      }
      this.getActivities();
    });
  }

  getActivities(){
    console.log("user" , this.Iduser)
    this.authservice.getUserActivities(this.Iduser).subscribe( data => {
      console.log("user",data)
      this.userActivities = []
      let date = new Date();
      let currentDate = date.getDate();
      let currentMonth = date.getMonth();
      let currentYear = date.getFullYear();
 
      for(let entry of data){
        let entryDate = new Date(entry.date);
        if(entryDate.getDate() == currentDate 
            && entryDate.getMonth() == currentMonth
            && entryDate.getFullYear() == currentYear){
              this.userActivities.push(entry);
        }
      }
      this.userActivities = data;
      if(!(this.userActivities === undefined || this.userActivities.length == 0)){
        let lastActivity = this.userActivities[this.userActivities.length - 1]["activityType"];
        if(lastActivity == "Punch In"){
          this.setPunchInElems();
        }else if(lastActivity == "Break In"){
          this.setBreakInElems();
        }else{
          this.checkedOut();
        }
      }
   });

  }

  reverseArray(array){
    return array.slice(0).reverse();
  }

  punchedIn(){
    var actType = "Punch In";
    this.date = new Date();
    let punchInTime = this.beautifyTime(this.date);
    this.storeActivity(actType, punchInTime);
  }

  setPunchInElems(){
    this.punchIn = false;
    this.punchOut = true;
    this.breakIn = true;
    this.breakOut = false;

  }

  punchedOut(){
    var actType = "Punch Out";
    this.date = new Date();
    let punchOutTime = this.beautifyTime(this.date);
    this.storeActivity(actType, punchOutTime);
  }


  breakedIn(){
    var actType = "Break In";
    this.date = new Date();
    let breakInTime = this.beautifyTime(this.date);
    this.storeActivity(actType, breakInTime);
  }

  setBreakInElems(){
    this.punchIn = false;
    this.punchOut = false;
    this.breakIn = false;
    this.breakOut = true;

  }

  breakedOut(){
    var actType = "Break Out";
    this.date = new Date();
    let breakOutTime = this.beautifyTime(this.date);
    this.storeActivity(actType, breakOutTime);
  }

  checkedOut(){
    this.punchIn = true;
    this.punchOut = false;
    this.breakIn = true;
    this.breakOut = false;

  }

  beautifyTime(date){
    var hour = date.getHours();
    var minute = date.getMinutes();
    var second = date.getSeconds();
    var ampm = hour >= 12 ? 'PM' : 'AM';
    hour = hour % 12;
    hour = hour ? hour : 12;
    var hour1 = ("0" + hour).slice(-2);
    var minute1 = ("0" + minute).slice(-2);
    var second1 = ("0" + second).slice(-2);
    return hour1+":"+minute1+":"+second1+" "+ampm;
  }

  clock(){
    let weekday = [
        "Sunday",
        "Monday", 
        "Tuesday", 
        "Wednesday", 
        "Thursday", 
        "Friday", 
        "Saturday",
        ];

    this.date = new Date();
    let day = weekday[this.date.getDay()];
    let dd = this.date.getDate();
    let mm = (this.date.getMonth()+1); //January is 0!
    let yyyy = this.date.getFullYear();
    
    let dd1 = ("0" + dd).slice(-2); 
    let mm1 = ("0" + mm).slice(-2); 
    let today = dd1 + '/' + mm1 + '/' + yyyy;

    var hour = this.date.getHours();
    var minute = this.date.getMinutes();
    var second = this.date.getSeconds();
    var ampm = hour >= 12 ? 'PM' : 'AM';
    hour = hour % 12;
    hour = hour ? hour : 12;
    var hour1 = ("0" + hour).slice(-2);
    var minute1 = ("0" + minute).slice(-2);
    var second1 = ("0" + second).slice(-2);

    document.getElementById('hour').innerHTML = hour1;
    document.getElementById('minute').innerHTML = ":"+minute1;
    document.getElementById('second').innerHTML = ":"+second1;
    document.getElementById('ampm').innerHTML = " "+ampm;
    document.getElementById('day').innerHTML = day;
    document.getElementById('date').innerHTML = today;
  }


}
