import { Component, OnInit,Inject, ViewEncapsulation, Renderer2, OnDestroy } from '@angular/core';
import {AdminService} from '../../../services/admin/admin.service';
import { DOCUMENT } from '@angular/common';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import {MAT_MOMENT_DATE_FORMATS, MomentDateAdapter} from '@angular/material-moment-adapter';


@Component({
  selector: 'app-action-calender',
  templateUrl: './action-calender.component.html',
  styleUrls: ['./action-calender.component.scss'],
  providers: [
    {provide: MAT_DATE_LOCALE, useValue: 'fr'},
    {provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
    {provide: MAT_DATE_FORMATS, useValue: MAT_MOMENT_DATE_FORMATS},
  ],
})

export class ActionCalenderComponent implements OnInit {
  form: FormGroup ;
  minDate: any;
  constructor(
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    private fb: FormBuilder,
    
    public dialogRef: MatDialogRef<ActionCalenderComponent>,
    private adminService : AdminService,
    private snackBar: MatSnackBar,
    @Inject(MAT_DIALOG_DATA) public data: any
    ) {
      this.form = this.fb.group({
        title: ['' , Validators.required],
        start: ['' , Validators.required ],
        end: ['', Validators.required],
        color :'',

       
     })
   
     this.minDate  = new Date()
    }

  ngOnInit(): void {

   console.log(this.data)
   if(this.data!== null)
   this.form.patchValue(this.data)
   console.log(this.data.color.primary)
   this.form.controls.color.patchValue(this.data.color.primary)
  }
  onSubmit(){
     if(this.data == null)
   this.addEvent()
    else
   this.updateevent()
  }
  addEvent(): void {
    let colors : any

    if (this.form.controls.color.value == '')
     colors = {
      primary: '#000000',
      secondary: '#000000'
    }
    else
    colors = {
      primary: this.form.controls.color.value ,
      secondary: '#FAE3E3'
    }
    this.adminService.addNote({
      title: this.form.controls.title.value,
      start: new Date(this.form.controls.start.value),
      end: new Date(this.form.controls.end.value),
     
      color: colors,
      draggable: true,
      resizable: {
        beforeStart: true,
        afterEnd: true,
      },
    })
    .subscribe(res => {
     
        if (res.success) {
          this.snackBar.open(res.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack'],

         });
       this.dialogRef.close();
        } else {
        this.snackBar.open(res.msg, 'X', {
           duration: 6000,
           verticalPosition: 'top',
           panelClass:['custom-snack-error'],
         });
         this.dialogRef.close();
        }
      
 
    });
  }

  updateevent() {
    let colors : any
    if (this.form.controls.color.value == '')
    colors = {
     primary: '#ad2121',
     secondary: '#FAE3E3'
   }
   else
   colors = {
     primary: this.form.controls.color.value ,
     secondary: '#FAE3E3'
   }
    this.adminService.UpdateNote({
      title: this.form.controls.title.value,
      start: new Date(this.form.controls.start.value),
      end: new Date(this.form.controls.end.value),
      color: colors,
      draggable: true,
      resizable: {
        beforeStart: true,
        afterEnd: true,
      },
      id: this.data.id
    })
    .subscribe(res =>  {
      if (res.success) {
      this.snackBar.open(res.msg, 'X', {
       duration: 6000,
       verticalPosition: 'top',
       panelClass:['custom-snack'],
     });
     this.dialogRef.close();
    } else {
    this.snackBar.open(res.msg, 'X', {
       duration: 6000,
       verticalPosition: 'top',
       panelClass:['custom-snack-error'],
     });
     this.dialogRef.close();
    }

    });
  }
 
  


  


 onNoClick(): void {
  this.dialogRef.close();
}

}
