import { NotfoundComponent } from './notfound/notfound.component';
import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';

import {AdminLayoutComponent} from './back-office/layouts/admin-layout/admin-layout.component'
import { LoginComponent } from './login/login.component';
import { FirstTimeComponent } from './firstTime/firstTime.component';
import { LockcScreenComponent } from './lockScreen/lockcScreen.component';
import {PreventLogin} from './services/auth/prevent.login' ;
import {AuthGuard} from './services/auth/auth.guard';
import { ClientGuard } from './services/auth/client.guard';
import {ClientLayoutComponent} from './front-office/client-layout/client-layout.component';
const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full',
  },
  {
    path: '',
    component: AdminLayoutComponent,
    children: [
        {
            path: '',
            loadChildren: () => import('./back-office/layouts/admin-layout/admin-layout.module').then(m => m.AdminLayoutModule)
        }], canActivate:[AuthGuard]
  },
   { path: 'login', component: LoginComponent, canActivate:[PreventLogin],
          data:{ title : 'Connexion'} },
   
          { path: 'firstTime', component: FirstTimeComponent, canActivate:[PreventLogin],
          data:{ title : 'Mise en place'} },
   
    { path: 'lockScreen', component: LockcScreenComponent, canActivate:[PreventLogin],
    data:{ title : 'Lock Screen'} },

   {
      path: '',
      component: ClientLayoutComponent,
      children: [
          {
              path: '',
              loadChildren: () => import('./front-office/client-layout/client-layout.module').then(m => m.ClientLayoutModule)
          }], canActivate:[ClientGuard]
   },

  {
    path: '**',
    component: NotfoundComponent,
    data: { title : '404 Not Found'}
  },
  
];

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes)
  ],
  exports: [
  ],
})
export class AppRoutingModule { }
