import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders   } from '@angular/common/http';
import { environment } from '../../../environments/environment'

@Injectable({
  providedIn: 'root'
})
export class AppeldoffreService {
  baseUrl = environment.baseUrl;

  constructor(private http: HttpClient) { }


  addAppel(appeldoffre) {
    return this.http.post<any>(this.baseUrl + '/api/Appeldoffre/create', appeldoffre, {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }


  deleteAppel(id: string) {
    const headers = new HttpHeaders();
    return this.http.delete<any>(this.baseUrl + '/api/Appeldoffre/' + id, {
        headers : new HttpHeaders().append('Content-type', 'application/json')
      }) ;
  }

  updateAppel(id: string,appeldoffre) {
    return this.http.put<any>(this.baseUrl + '/api/Appeldoffre/update/' + id, appeldoffre, {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  updateAppelFiles(id: string,appeldoffre) {
    return this.http.put<any>(this.baseUrl + '/api/Appeldoffre/updateFiles/' + id, appeldoffre, {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }
  
  addFiles(id: string,appeldoffre) {
    return this.http.put<any>(this.baseUrl + '/api/Appeldoffre/addFiles/' + id, appeldoffre, {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  getAllAppels() {
    return this.http.get<any>(this.baseUrl + '/api/Appeldoffre', {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  getRefs() {
    return this.http.get<any>(this.baseUrl + '/api/Appeldoffre/refs', {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  getOneAppel(id: string) {
    const headers = new HttpHeaders();
    let user: any;
    return this.http.get<any>(this.baseUrl + '/api/Appeldoffre/' + id, {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  addCategorie(cat) {
    return this.http.post<any>(this.baseUrl + '/api/categoryAppel/create', cat, {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  getCategories() {
    return this.http.get<any>(this.baseUrl + '/api/categoryAppel', {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  addDocument(cat) {
    return this.http.post<any>(this.baseUrl + '/api/documentAppel/create', cat, {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  getDocuments() {
    return this.http.get<any>(this.baseUrl + '/api/documentAppel', {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }


  addRaison(raison) {
    return this.http.post<any>(this.baseUrl + '/api/raisonAppel/create', raison, {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  getRaisons() {
    return this.http.get<any>(this.baseUrl + '/api/raisonAppel', {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  addModalite(cat) {
    return this.http.post<any>(this.baseUrl + '/api/modaliteDepot/create', cat, {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  getModalites() {
    return this.http.get<any>(this.baseUrl + '/api/modaliteDepot', {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  getGroupes() {
    const headers = new HttpHeaders();
    return this.http.get<any>(this.baseUrl + '/api/groupe', {
      headers : new HttpHeaders().append('Content-type', 'application/json')
    });
  }

  downloadDoc(file: String){
     const body = {filename : file};
     return this.http.post<any>(this.baseUrl + '/api/documents/download',body,{
       responseType : 'blob' as 'json',
       headers : new HttpHeaders().append('Content-type', 'application/json')

     })
  }

}
