import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import { AdminService } from '../../services/admin/admin.service';
import {AuthService} from '../../services/auth/auth.service'
import {emailValidation, passValidation} from '../../services/validations';
import {FileUploader } from 'ng2-file-upload';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import {Router, ActivatedRoute } from '@angular/router';
import { environment } from '../../../environments/environment';
const uploadUri = environment.uri;
@Component({
  selector: 'app-profil-client',
  templateUrl: './profil-client.component.html',
  styleUrls: ['./profil-client.component.scss']
})
export class ProfilClientComponent implements OnInit {

  form: FormGroup ;
  user : any ;
  id : any ;
  loginIn : true ;
  filePreviewPath : SafeUrl
  uploader: FileUploader = new FileUploader({url: uploadUri});
  constructor(private snackBar: MatSnackBar,
    private adminService: AdminService,
    public authservice: AuthService,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private sanitizer: DomSanitizer,
    ) { 
      this.uploader.onAfterAddingFile = (fileItem) => {
        this.filePreviewPath  = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(fileItem._file)));
        //this.imageChange();
      }
      this.form = this.fb.group({
        password: ['', Validators.required],
        cpassword: new FormControl('', [
          passValidation,
        ]),
      });
    }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      if(params['id'])
      this.id = params['id'];
      else
    this.id=this.authservice.getUserfromToken()._id;
    this.getoneclient(this.id)
  })
  
  }
getoneclient(id){

  this.adminService.getOneClient(id).subscribe((res) => this.user = res );
}
  onSubmit() {
    if (!this.form.invalid && this.form.controls.password.value !== '') {
     
      let client = {
        password : this.form.controls.password.value
      };
      this.adminService.updateClient(this.id, client).subscribe(data => {
        console.log(data);
        if (data.success) {
         
          this.snackBar.open(data.msg , 'X', {
                 duration: 6000,
                 verticalPosition: 'top',
                 panelClass:['custom-snack'],
               });
        } else {
        console.log(data);
        this.snackBar.open(data.msg , 'X', {
                 duration: 6000,
                 verticalPosition: 'top',
                 panelClass:['custom-snack-error'],
               });
      
        }
       });
    }
  }
}
