import { Component, OnInit } from '@angular/core';
import {AuthService} from '../services/auth/auth.service';
import {Router, ActivatedRoute } from '@angular/router';
import { environment } from '../../environments/environment';

const awsUrlUri = environment.awsUrl;

@Component({
  selector: 'app-lockcScreen',
  templateUrl: './lockcScreen.component.html',
  styleUrls: ['./lockcScreen.component.scss']
})
export class LockcScreenComponent implements OnInit {
  password : string ;
  error : string ;
  validation:string;
  loginIn = false;
  id: string;
  user = {
    email:'',
    username:'',
    image:'',
  };
  image='';

  constructor(
     private authservice:AuthService,
     private router:Router,
     private activatedRoute: ActivatedRoute,
     ) { 
      this.activatedRoute.queryParams.subscribe(params => {
      if(params['id'] && params['id'] !== 'false'){
        this.id = params['id'];
        this.authservice.getOneUserLogin(this.id)
          .subscribe((res) => {
            if(!res.success){
            this.router.navigate(['/login']);
          }else{
            this.user = res.user[0];
            console.log(this.user);
            this.image = awsUrlUri + this.user.image ;
          }
          }, err => {
            console.log(err);
          });    
      }else{
        this.router.navigate(['/login']);
      }
    });
  }


  ngOnInit() {

  }

  onSubmit(){
    this.loginIn = true;
    const user = {
      email : this.user.email,
      password : this.password
    }
      if(!this.user.email || !this.password){
        this.validation = 'Les champs email et mot de passe sont obligatoires';
        this.loginIn = false;
      } else {
      this.authservice.authenticateUser(user).subscribe(data =>{
       if(data.success){
          this.authservice.sotreUserData(data.token, data.user);
          this.router.navigate(['/dashboard']);
       } else{
        this.authservice.authenticateClient(user).subscribe(data =>{
          if(data.success){
             this.authservice.sotreUserData(data.token, data.client);
             this.router.navigate(['/home']);
          } else{
            this.error = data.msg ; 
          }
         });
       }
       this.loginIn = false;
      });
    }
  }

}
