  async checkToken(){
    if(this.loggedIn() && this.getIdfromToken()!= 'false'){
      let body = {
        token: this.getToken()
      }
       let loggedIn = await this.http.post<any>(this.baseUrl + '/api/shared/checkToken', body, {
        headers : new HttpHeaders().append('Content-type', 'application/json')
      }).toPromise();
        
      console.log(loggedIn);
        if(!loggedIn){
          this.introJS = false;
          this.exitingIntroJs();
          this.router.navigate(['/lockScreen'], { queryParams: { id: this.getIdfromToken() } });
          this.logout();
        }else{
          return true;
        }
    }else{
      this.router.navigate(['/lockScreen'], { queryParams: { id: this.getIdfromToken() } });
      this.logout();
    }
  }

