import { Component, OnInit } from '@angular/core';
import {AuthService} from '../services/auth/auth.service';
import {Router} from '@angular/router' ;


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  email : string ;
  password : string ;
  error : string ;
  validation:string;
  loginIn = false;
  

  constructor(
     private authservice:AuthService,
     private router:Router
     ) { 

  }

  ngOnInit() {
  }

  isMobileMenu() {
    if ($(window).width() > 991) {
        return false;
    }
    return true;

};

  onLoginSubmit(){
    this.loginIn = true;
    const user = {
      email : this.email,
      password : this.password
    }
      if(!this.email || !this.password){
        this.validation = 'Les champs email et mot de passe sont obligatoires';
        this.loginIn = false;
      } else {
      this.authservice.authenticateUser(user).subscribe(data =>{
       if(data.success){
          this.authservice.sotreUserData(data.token, data.user);
          console.log(data);
          if(data.grade && data.grade == 100){
             this.router.navigate(['/dashboard']);
            } else{
              this.router.navigate(['/user-profile']);
            }
       } else{
        this.authservice.authenticateClient(user).subscribe(data =>{
          if(data.success){
             this.authservice.sotreUserData(data.token, data.client);
             this.router.navigate(['/home']);
          } else{
            this.error = data.msg ; 
          }
         });
       }
       this.loginIn = false;
      });
    }
  }

}
